'use client'

import { useState } from 'react'
import Image from 'next/image'
import Link from 'next/link'
import { Card, CardContent } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

// Mock chat data
const mockChats = [
  {
    id: '1',
    petName: 'Buddy',
    ownerName: 'Sarah Johnson',
    image: 'https://images.unsplash.com/photo-1552053831-71594a27632d?w=300&h=300&fit=crop',
    lastMessage: 'Hi! Buddy would love to meet Luna for a playdate!',
    lastMessageTime: '2024-01-15T10:30:00Z',
    unreadCount: 2,
    isOnline: true
  },
  {
    id: '2',
    petName: 'Luna',
    ownerName: 'Mike Chen',
    image: 'https://images.unsplash.com/photo-1551717743-49959800b1f6?w=300&h=300&fit=crop',
    lastMessage: 'That sounds great! When would work best?',
    lastMessageTime: '2024-01-15T09:15:00Z',
    unreadCount: 0,
    isOnline: false
  },
  {
    id: '3',
    petName: 'Max',
    ownerName: 'Emily Davis',
    image: 'https://images.unsplash.com/photo-1589941013453-ec89f33b5e95?w=300&h=300&fit=crop',
    lastMessage: 'Max is very well-behaved and would be perfect for breeding.',
    lastMessageTime: '2024-01-14T16:45:00Z',
    unreadCount: 1,
    isOnline: true
  }
]

export default function ChatPage() {
  const [chats] = useState(mockChats)

  const formatTime = (timeString: string) => {
    const date = new Date(timeString)
    const now = new Date()
    const diffTime = Math.abs(now.getTime() - date.getTime())
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
    
    if (diffDays === 1) return 'Yesterday'
    if (diffDays < 7) return `${diffDays}d ago`
    return date.toLocaleDateString()
  }

  if (chats.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center min-h-screen p-6">
        <div className="text-center">
          <div className="text-6xl mb-4">ðŸ’¬</div>
          <h2 className="text-2xl font-bold mb-4">No conversations yet</h2>
          <p className="text-gray-600 mb-6">
            Start matching with pets to begin conversations!
          </p>
          <Link href="/">
            <button className="bg-primary text-white px-6 py-2 rounded-lg hover:bg-primary/90">
              Find Matches
            </button>
          </Link>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-6">
      <div className="mb-6">
        <h1 className="text-2xl font-bold mb-2">Messages</h1>
        <p className="text-gray-600">
          {chats.length} conversation{chats.length !== 1 ? 's' : ''}
        </p>
      </div>

      <div className="space-y-2">
        {chats.map((chat) => (
          <Link key={chat.id} href={`/chat/${chat.id}`}>
            <Card className="hover:shadow-md transition-shadow cursor-pointer">
              <CardContent className="p-4">
                <div className="flex items-center space-x-4">
                  {/* Profile Image with Online Status */}
                  <div className="relative">
                    <div className="relative w-12 h-12 rounded-full overflow-hidden">
                      <Image
                        src={chat.image}
                        alt={chat.petName}
                        fill
                        className="object-cover"
                        sizes="48px"
                      />
                    </div>
                    {chat.isOnline && (
                      <div className="absolute -bottom-1 -right-1 w-4 h-4 bg-green-500 border-2 border-white rounded-full"></div>
                    )}
                  </div>

                  {/* Chat Info */}
                  <div className="flex-1 min-w-0">
                    <div className="flex items-center justify-between mb-1">
                      <h3 className="font-semibold text-sm truncate">
                        {chat.petName}
                      </h3>
                      <span className="text-xs text-gray-500 flex-shrink-0">
                        {formatTime(chat.lastMessageTime)}
                      </span>
                    </div>
                    
                    <p className="text-xs text-gray-600 mb-1">
                      Owner: {chat.ownerName}
                    </p>
                    
                    <div className="flex items-center justify-between">
                      <p className="text-sm text-gray-700 truncate flex-1">
                        {chat.lastMessage}
                      </p>
                      
                      {chat.unreadCount > 0 && (
                        <Badge className="ml-2 bg-primary text-white text-xs min-w-[20px] h-5 flex items-center justify-center rounded-full">
                          {chat.unreadCount}
                        </Badge>
                      )}
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </Link>
        ))}
      </div>

      {/* Quick Actions */}
      <div className="mt-8 text-center">
        <p className="text-gray-600 mb-4">Want to find more pets to chat with?</p>
        <div className="space-x-4">
          <Link href="/">
            <button className="bg-primary text-white px-4 py-2 rounded-lg hover:bg-primary/90">
              Keep Swiping
            </button>
          </Link>
          <Link href="/matches">
            <button className="border border-gray-300 text-gray-700 px-4 py-2 rounded-lg hover:bg-gray-50">
              View Matches
            </button>
          </Link>
        </div>
      </div>
    </div>
  )
}
